import styled, { css } from "styled-components";
import { DEFAULT_WHITE, ACTIVATE_GREEN, DEACTIVATE_RED, NAV_GRAY } from "../../../consts/ColorCodes";


export default function Activate(props) {

    let ActivateValue = getActivate(props.value)
    
    const ActivateStyle = css`
        ${(props) =>
            props.value === "" &&
            css`
                width: 80px;
                height: 25px;
                line-height: 25px;
                border-radius: 5px;
                color: ${DEFAULT_WHITE};
                background-color: ${ACTIVATE_GREEN};
            `
        }

        ${(props) =>
            props.value === "" &&
            css`
                width: 80px;
                height: 25px;
                line-height: 25px;
                border-radius: 5px;
                color: ${DEFAULT_WHITE};
                background-color: ${DEACTIVATE_RED};
            `
        }

        ${(props) =>
            props.value === "" &&
            css`
                width: 80px;
                height: 25px;
                line-height: 25px;
                border-radius: 5px;
                color: ${DEFAULT_WHITE};
                background-color: ${NAV_GRAY};
            `
        }

        ${(props) =>
            props.value === "" &&
            css`
                width: 80px;
                height: 25px;
                line-height: 25px;
                border-radius: 5px;
                color: ${DEFAULT_WHITE};
                background-color: ${ACTIVATE_GREEN};
            `
        }

        ${(props) =>
            props.value === "" &&
            css`
                width: 80px;
                height: 25px;
                line-height: 25px;
                border-radius: 5px;
                color: ${DEFAULT_WHITE};
                background-color: ${DEACTIVATE_RED};
            `
        }

    `

    const Activate = styled.div`
        text-align: center;
        font-size: 12px;
        ${ActivateStyle}
    `

    return (
        <Activate value={ActivateValue}>{ActivateValue}</Activate>
    )
}

function getActivate(activate) {
    if (activate === true) {
        return ""
    } else if (activate === false) {
        return ""
    } else if (activate === "NOT_STARTED") {
        return "" 
    } else if (activate === "IN_PROGRESS") {
        return ""
    } else if (activate === "COMPLETE") {
        return ""
    }
}